* {
    margin: 0;
    padding: 0;
}

ul,
li {
    list-style: none;
}

a {
    text-decoration: none;
}

.Home {
    text-align: center;
}

.Home-header-image span {
    background-image: url(../assets/buildings.jpg);
    width: 1300px;
    height: 300px;
    margin-bottom: 100px;
    background-size: 1300px 400px;
    display: inline-block;
    background-repeat: no-repeat;
}

.Home-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Home-container {
    width: 1300px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
}
.Home-table-container {
    /* width: 1300px; */
    float: left;
}
.Home-table-container table {
    width: 960px;
    margin-bottom: 300px;
}

.Home-login-container {
    width: 290px;
    height: 210px;
    margin-left: 50px;
    text-align: center;
    /* margin-top: 60px; */
    /* float: left; */
    border-radius: 14px;
    background-color: rgb(242, 242, 242);
}

.Home-login-container form {
    display: inline-block;
    margin-top: 25px;
    /* padding-left: 50px; */
}
.Home-login-container form input {
    /* float: left; */
    width: 160px;
    height: 22px;
    /* margin-left: 10px; */
    margin-bottom: 10px;
    border: 1px solid rgb(173, 173, 173);
    border-radius: 2px;
}
.Home-login-container form label {
    float: left;
    font-size: 17px;
    font-weight: 550;
    color: rgb(48, 48, 48);
}

.Home-login-container button {
    margin-top: 10px;
    height: 30px;
    width: 80px;
}
.Home-loginBtn {
    /* margin-left: -10px; */
    background-color: rgb(60, 161, 234);
    color: white;
    font-weight: bold;
    border: 0px;
    border-radius: 5px;
}

.Home-signinBtn {
    margin-left: 10px;
    background-color: white;
    font-weight: bold;
    border: 0px;
    border-radius: 5px;
}
