* {
    margin: 0;
    padding: 0;
}
.BidInfo {
    text-align: center;
}

.option-container {
    width: 1300px;
    display: inline-block;
    margin-bottom: 10px;
}

.BidInfo form {
    float: right;
}
.BidInfo form input {
    height: 22px;
    width: 200px;
}
.BidInfo form button {
    margin-left: 5px;
    height: 25px;
    width: 60px;
}
.filterBtn {
    float: left;
}

.filterBtn button {
    width: 65px;
    margin-right: 7px;
}
