.common-table {
    width: 1300px;
    margin: 0 auto;
    text-align: center;
    border-spacing: 0;
}

.common-table-header-column {
    border-bottom: 1px solid #e9e9e9;
    border-top: 1px solid #e9e9e9;
    padding: 0;
    font-size: 16px;
    padding: 10px 5px;
    font-weight: bold;
}

.common-table-row:hover {
    background-color: #eceaea;
}

.common-table-column {
    border-bottom: 1px solid #eeeded;

    padding: 10px 5px;
}

.common-table-column span {
    margin-left: 3px;
    border-radius: 5px;
    font-size: 14px;
    padding: 1px 2px 1px 3px;
}

.bidLabel-e {
    background-color: rgb(252, 255, 85);
}

.bidLabel-r {
    background-color: rgb(145, 239, 255);
}
a {
    text-decoration: none;
    color: black;
    cursor: pointer;
}
