.r-bid-form-container {
    padding-left: 240px;
    padding-right: 240px;
    width: 700px;
    margin: 0 auto;
    margin-top: 55px;
    margin-bottom: 300px;
    text-align: left;

    /* padding-left: 30%; */
    /* text-align: center; */
    border-spacing: 0;
    border-top: 1px solid rgb(95, 95, 95);
}

.r-bid-form-container form {
    display: inline-block;
    margin-top: 50px;
}

.r-bid-form-container table {
