* {
    margin: 0;
    padding: 0;
}

ul,
li {
    list-style: none;
}

a {
    text-decoration: none;
}

/* .Home {
    text-align: center;
} */

.header-nav-container {
    /* border-top: 2px solid #004385; */
    width: 1199px;
    height: 70px;
    display: table;
    margin-left: auto;
    margin-right: auto;
}
.header-nav-container li {
    display: inline-block;
}

.header-logo {
    /* width: 203px; */
    display: inline-block;
    float: left;
}
.header-logo-img {
    background-image: url(../../assets/logo.gif);
    margin-top: 20px;
    width: 203px;
    height: 26px;
}
.header-menu {
    display: table-cell;
    vertical-align: middle;
}
.header-menu > ul > li > a {
    font-size: 19px;
    color: #004385;
    font-weight: bold;
    margin-right: 25px;
    margin-left: 25px;
}

.header-member {
    float: right;
    display: table-cell;
    vertical-align: middle;
}
.header-member > ul > li > a {
    font-size: 17px;
    color: gray;
    font-weight: bold;
    margin-left: 20px;
}

.header-member button {
    font-size: 17px;
    color: gray;
    font-weight: bold;
    margin-left: 20px;
    padding-left: 5px;
    padding-right: 5px;
}
