* {
    margin: 0;
    padding: 0;
}

.Login {
    text-align: center;
}

.Login-h1 {
    margin-top: 22px;
    margin-bottom: 22px;
}
.Login-form-container {
    border: 1px solid rgb(95, 95, 95);
    border-radius: 4px;
    width: 550px;
    height: 350px;
    margin-left: auto;
    margin-right: auto;
    /* text-align: center; */
}

.Login-form-container form {
    display: inline-block;
    margin-top: 55px;
}
.Login-form-container form input {
    /* float: left; */
    width: 270px;
    height: 39px;
    margin-left: 10px;
    border: 1px solid rgb(173, 173, 173);
    border-radius: 2px;
}
.Login-form-container form label {
    float: left;
    width: 100px;
    margin-left: -11px;
    margin-top: 5px;
    font-size: 19px;
    font-weight: 440;
    color: rgb(48, 48, 48);
}

.Login-form-container button {
    width: 370px;
    height: 50px;
    font-size: 15px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.Login-loginBtn {
    margin-top: 30px;

    background-color: rgb(60, 161, 234);
    color: white;
    font-weight: bold;
    border: 0px;
    border-radius: 5px;
}
.Login-signinBtn {
    margin-top: 15px;
    font-weight: bold;
    border: 0px;
    border-radius: 5px;
}
